body {
	font-family: 'Rubik', sans-serif;
}
.hero {
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	position: relative;
}
.hero .overlay {
	z-index: 0;
	position: absolute;
	inset: 0 0 0 0;
	background-color: rgba(0, 0, 0, 0.2);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.hero video {
	position: fixed;
	right: 0;
	bottom: 0;
	min-width: 100%;
	min-height: 100%;
	z-index: -1;
}
form input {
	font-size: 21px;
	padding: 10px 20px;
	background-color: transparent;
	color: white;
	border: none;
	border-bottom: 2px solid white;
	text-align: center;
}
form input::placeholder {
	color: rgba(255, 255, 255, 0.5);
}
form input:focus,
input:active {
	outline: none;
}
.card {
	color: white;
	font-size: 24px;
	display: flex;
	align-items: center;
	margin-top: 50px;
}
.card .temperature {
	font-size: 68px;
	margin-bottom: 20px;
}

/* MEDIA QUERIES */

@media screen and (min-width: 1200px) {
	.hero .overlay {
		flex-direction: row;
	}
	.card {
		position: absolute;
		right: 10%;
		margin-top: 0;
	}
}
